<template>
  <div class="object">
    <div class="title">{{title || $t('sub_property')}}</div>
    <template v-if="field">
      <div class="field"
           v-for="{field,prop} in useableFields"
           :key="prop">
        <Field :field="field"
               :prop="prop">
        </Field>
      </div>
    </template>
  </div>
</template>

<script>

export default {
  name: 'ObjectType',
  components: {
    Field: () => import('./Field.vue'),
  },
  props: {
    title: String,
    field: Object,
  },
  data() {
    return {
      publicProps: ['$name', '$desc', '$required', '$order', '$type', '$default', '$length', '$min_length', '$max_length', '$count', '$min_count', '$max_count'],
    };
  },
  computed: {
    useableFields() {
      const bd = [];
      Object.keys(this.field)
        .filter(key => !this.publicProps.includes(key))
        .forEach((key) => {
          bd.push({
            prop: key,
            field: this.field[key],
          });
        });
      return bd.map((item) => {
        const v = item.field;
        if (Array.isArray(v)) {
          return '$order' in v[0] ? item : ({ ...item, field: [{ ...v[0], $order: 0 }] });
        }
        return '$order' in v ? item : ({ ...item, field: { ...v, $order: 0 } });
      })
        .sort((aItem, bItem) => {
          const a = aItem.field;
          const b = bItem.field;
          const av = Array.isArray(a) ? a[0] : a;
          const bv = Array.isArray(b) ? b[0] : b;
          return av.$order - bv.$order;
        })
        .map((item) => {
          const v = item.field;
          if (Array.isArray(v)) {
            const { $order, ...otherValue } = v[0];
            return {
              ...item,
              field: [otherValue],
            };
          }
          const { $order, ...otherValue } = v;
          return {
            ...item,
            field: otherValue,
          };
        });
    },
  },
};
</script>

