<template>
  <div class="type">
    <div class="row">
      <div class="title">
        <template v-if="title">{{title}}</template>
        <slot name="title"></slot>
      </div>

      <el-switch v-model="useRaw"
                 :active-text="$t('switch_raw')" />
    </div>
    <Code v-if="useRaw"
          :json="optBody"
          :title="title" />
    <template v-else>
      <div class="body"
           v-if="!(isSimpleType || isArray)">
        <div class="field"
             v-for="{field,prop} in useableBody"
             :key="prop">
          <Field :field="field"
                 :prop="prop" />
        </div>
      </div>
      <ArrayType v-else-if="isArray"
                 :field="body[0]">
      </ArrayType>
      <div class="simple-body"
           v-else-if="isSimpleType">
        <span class="name">{{body.name}}</span>
        <span class="desc">{{body.desc}}</span>
      </div>
      <div class="none"
           v-else>{{$t('none_body')}}</div>
    </template>
  </div>
</template>

<script>
import { Switch, Message } from 'element-ui';
import Field from './ComplexType/Field.vue';
import ArrayType from './ComplexType/ArrayType.vue';
import Code from '../Code.vue';

export default {
  name: 'simple-type',
  components: {
    ArrayType,
    Field,
    Code,
    'el-switch': Switch,
  },
  props: {
    title: String,
    isObject: Boolean,
    body: [Object, Array],
  },
  watch: {
    '$route.query': {
      immediate: true,
      handler() {
        this.useRaw = false;
      },
    },
  },
  data() {
    return {
      useRaw: false,
      publicProps: ['$name', '$desc', '$required', '$type', '$default', '$order'],
    };
  },
  computed: {
    optBody() {
      return JSON.parse(JSON.stringify(this.body, (k, v) => (['$type', '$order'].includes(k) ? undefined : v)));
    },
    useableBody() {
      const bd = [];
      if (typeof this.body === 'object' && this.body.$type === 'object') {
        Object.keys(this.body)
          .filter(key => !this.publicProps.includes(key))
          .forEach((key) => {
            bd.push({
              prop: key,
              field: this.body[key],
            });
          });
      }
      return bd
        .map((item) => {
          const v = item.field;
          if (Array.isArray(v)) {
            if (v[0]) {
              return '$order' in v[0] ? item : ({ ...item, field: [{ ...v[0], $order: 0 }] });
            }
            Message.error({
              message: `${this.title}.${item.prop} is Empty!`,
              duration: 8000,
            });
            return item;
          }
          return '$order' in v ? item : ({ ...item, field: { ...v, $order: 0 } });
        })
        .sort((aItem, bItem) => {
          const a = aItem.field;
          const b = bItem.field;
          const av = Array.isArray(a) ? a[0] : a;
          const bv = Array.isArray(b) ? b[0] : b;
          return av && bv ? av.$order - bv.$order : 0;
        })
        .map((item) => {
          const v = item.field;
          if (Array.isArray(v)) {
            if (v[0]) {
              const { $order, ...otherValue } = v[0];
              return {
                ...item,
                field: [otherValue],
              };
            }
            return item;
          }
          const { $order, ...otherValue } = v;
          return {
            ...item,
            field: otherValue,
          };
        });
    },
    isArray() {
      return Array.isArray(this.body);
    },
    isSimpleType() {
      return typeof this.body === 'object' && typeof this.body.type === 'string';
    },
  },
};
</script>

<style lang="scss" scoped>
.type {
  .row {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #ddd;
    margin-bottom: 10px;
    .title {
      padding: 10px 0;
      font-weight: 600;
      font-size: 1.2rem;
      margin-right: 20px;
    }
  }

  .field {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }

  .simple-body {
    margin-top: 10px;
    .desc {
      margin-left: 10px;
      color: #aaa;
    }
  }

  .none {
    margin: 10px 0;
  }
}
</style>
